body {
  background-color: #fff !important;
}

.bgnew {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 100vh;
  opacity: 0.8;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.splash-content header {
  color: #fff;
  font-weight: 900;
  font-size: 2.925em;
  margin-bottom: 3%;
  text-shadow: 2px 3px 0px #898999;
}

section {
  padding-bottom: 30px;
}
section > div.row, section > form > div.row {
  margin-left: 0px;
  margin-right: 0px;
}
section h1 {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #20376b;
  position: relative;
}
section h1 span {
  font-weight: normal;
}
section h1:after {
  white-space: pre;
  color: #20376b;
  border-left: 2px solid #f1685c;
  font-size: 1rem;
  display: block;
  line-height: 1.3rem;
  padding-left: 10px;
  position: absolute;
  top: 20%;
  right: 0%;
}
section h1.splash-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  text-transform: none;
  min-width: 500px;
}
section h1.splash-header img {
  width: 164px;
}
section h1.splash-header i {
  font-size: 75px;
}
section h1.splash-header:after {
  display: none;
}
section h3 {
  color: #20376b;
  margin-top: 0px;
}

form#fileupload div.row, div#fileupload div.row {
