  padding-left: 8px;
}
form#fileupload .fileupload-buttonbar .toggle[type=checkbox], div#fileupload .fileupload-buttonbar .toggle[type=checkbox] {
  transform: scale(1.2);
  margin-right: 14px;
}
form#fileupload .fileupload-buttonbar button, div#fileupload .fileupload-buttonbar button {
  margin: 0px;
}
form#fileupload .fileupload-buttonbar .btn, div#fileupload .fileupload-buttonbar .btn {
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.6rem;
}
form#fileupload .table tbody tr, div#fileupload .table tbody tr {
  border-top: 1px solid #ddd;
}
form#fileupload .table tbody tr td, div#fileupload .table tbody tr td {
  border-top-width: 0px;
}
form#fileupload .table .preview-cell, div#fileupload .table .preview-cell {
  width: 90px;
  text-align: center;
}
form#fileupload .table .checkbox-cell, div#fileupload .table .checkbox-cell {
  width: 40px;
  text-align: center;
}
form#fileupload .table .checkbox-cell .toggle[type=checkbox], div#fileupload .table .checkbox-cell .toggle[type=checkbox] {
  transform: scale(1.1);
  margin: 0px;
}
form#fileupload .table .name-cell, div#fileupload .table .name-cell {
  width: 50%;
}
form#fileupload .table .size-cell, div#fileupload .table .size-cell {
  width: 30%;
}
form#fileupload .table .btn-cell, div#fileupload .table .btn-cell {
  display: flex;
  justify-content: flex-end;
  gap: 6px;
  width: 100px;
