form#fileupload .table .btn-cell button.btn, div#fileupload .table .btn-cell button.btn {
  margin: 0;
  padding: 5px 10px;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  border-radius: 3px;
}
form#fileupload .table .btn-cell button.btn i, div#fileupload .table .btn-cell button.btn i {
  margin-right: 0px;
}
form#fileupload input[type=text].form-control,
form#fileupload textarea.form-control, div#fileupload input[type=text].form-control,
div#fileupload textarea.form-control {
  width: 80%;
}
form#fileupload .checkbox input[type=checkbox],
form#fileupload .checkbox-inline input[type=checkbox],
form#fileupload .radio input[type=radio],
form#fileupload .radio-inline input[type=radio], div#fileupload .checkbox input[type=checkbox],
div#fileupload .checkbox-inline input[type=checkbox],
div#fileupload .radio input[type=radio],
div#fileupload .radio-inline input[type=radio] {
  margin-top: 7px;
}
form#fileupload select.form-control, div#fileupload select.form-control {
  width: 200px;
}
form#fileupload textarea.form-control, div#fileupload textarea.form-control {
  min-height: 50px;
}
form#fileupload button i, div#fileupload button i {
  display: inline-block;
  margin-right: 6px;
}
form#fileupload li, div#fileupload li {
  font-size: 1.15em;
}
form#fileupload ol ol, div#fileupload ol ol {
  padding-left: 2em;
  font-size: 1.2em;
}
form#fileupload ol ol > li, div#fileupload ol ol > li {
  list-style-type: lower-roman;
}
form#fileupload p, div#fileupload p {
  font-size: 0.9em;
  padding: 0.6em;
}
form#fileupload button, div#fileupload button {
  margin: 1em 1em 1em 0;
}

.container_iframe .bg-info {
  background-color: rgba(117, 202, 235, 0.6);
}
.container_iframe .bg-info p {
  color: #333;
}

/*// Drop Zone //////////////////////*/
#dropzone {
  width: 0px;
  height: 0px;
  min-height: 0px;
  line-height: 0px;
  text-align: center;
  font-weight: bold;
  display: grid;
  align-content: center;
  position: absolute;
  left: 0px;
  top: 0px;
  opacity: 0;
  background-color: rgba(245, 251, 249, 0.7);
  color: #848484;
}
#dropzone.in {
  width: 100%;
  height: 100%;
  font-size: larger;
  border-radius: 0;
  opacity: 1;
}
#dropzone.fade {
  -webkit-transition: opacity 1s ease-out;
  -moz-transition: opacity 1s ease-out;
  -ms-transition: opacity 1s ease-out;
  -o-transition: opacity 1s ease-out;
  transition: opacity 1s ease-out;
}